@font-face {
  font-family: 'Font_fun';
  src: url(/static/media/Exo2.0-Thin.44bde874.otf);
}
html {
  background: #000 url(/static/media/bg.9c94c76c.jpg) repeat;
  height: 100%;
}
body,
#root {
  min-width: 320px;
  display: flex;
  flex-direction: column;
  height: 100vh;
}
body *, html * {
  font-family: 'Trebuchet MS', Arial, sans-serif;
  -webkit-font-smoothing: antialiased !important;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility:    hidden;
  -ms-backface-visibility:     hidden;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
.page {
  min-height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1 0 auto;
}
.wrapper {
  display: block;
  position: relative;
  width: 100%;
  max-width: 1240px;
  padding: 20px;
  box-sizing: border-box;
  height: auto;
}
.ttl1 {
  font-size: 36px;
  color: #fff;
  text-align: center;
  margin: 0;
  font-family: 'Font_fun', Arial, sans-serif;
  font-weight: 100;
}
.card-list {
  margin-top: 23px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.card__block {
  width: 100%;
  max-width: 320px;
  margin: 0 40px 20px;
  box-sizing: border-box;
}
.card {
  display: block;
  position: relative;
  background: -webkit-linear-gradient(315deg, transparent 30px, #1698d9 0);
  background: linear-gradient(135deg, transparent 30px, #1698d9 0);
  height: 472px;
  padding: 4px;
  border-radius: 10px;
  text-decoration: none;
}
.card__wrap {
  position: relative;
  background: url(/static/media/cat.80f8d96c.png) no-repeat -29px calc(100% + 92px),
  -webkit-linear-gradient(315deg, transparent 28px, #fff 0);
  background: url(/static/media/cat.80f8d96c.png) no-repeat -29px calc(100% + 92px),
  linear-gradient(135deg, transparent 28px, #fff 0);
  border-radius: 6px;
  box-sizing: border-box;
  padding: 17px 43px;
  height: 100%;
}
.card__product {
  color: #666;
  font-size: 16px;
  max-width: 220px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  margin: 0;
}
.card__product--hover {
  display: none;
}
.card__ttl2 {
  font-size: 48px;
  color: #000;
  font-weight: bold;
  margin: 5px 0 0 0;
}
.card__ttl-sub {
  font-size: 24px;
  font-weight: bold;
  margin: -2px 0 14px 0;
  color: #000;
  max-width: 220px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.card__descr {
  font-size: 14px;
  color: #666;
  line-height: 16px;
  margin: 0;
  font-weight: normal;
  max-width: 220px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.bold {
  font-weight: bold;
}
.card__weight {
  position: absolute;
  bottom: 12px;
  right: 11px;
  background: #1698d9;
  border-radius: 50%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  width: 80px;
  height: 80px;
  z-index: 10;
}
.card-weight__big {
  width: 100%;
  font-size: 42px;
  color: #fff;
  text-align: center;
  margin: 14px 0 0 0;
  line-height: 22px;
}
.card-weight__sub {
  font-size: 21px;
  color: #fff;
  text-align: center;
  margin: -9px 0 0 0;
  line-height: 22px;
}
.card__bot-text {
  text-align: center;
  position: relative;
  color: #fff;
  font-size: 13px;
  margin: 14px 0 0 0;
  max-width: 320px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.card__bot-text a {
  color: #1698d9;
  border-bottom: 1px dashed #1698d9;
}
.card__block:hover .card  {
  background: -webkit-linear-gradient(315deg, transparent 30px, #2ea8e6 0);
  background: linear-gradient(135deg, transparent 30px, #2ea8e6 0);
}
.card__block:hover .card__weight {
  background: #2ea8e6;
}
.card__bot-text--disabled,
.card__bot-text--selected {
  display: none;
}
.card__block.selected .card {
  background: -webkit-linear-gradient(315deg, transparent 30px, #d91667 0);
  background: linear-gradient(135deg, transparent 30px, #d91667 0);
}
.card__block.selected  .card__weight {
  background: #d91667;
}
.card__block.selected .card__bot-text--default {
  display: none;
}
.card__block.selected .card__bot-text--selected {
  display: block;
}
.card__block.selected.hover .card {
  background: -webkit-linear-gradient(315deg, transparent 30px, #e62e7a 0);
  background: linear-gradient(135deg, transparent 30px, #e62e7a 0);
}
.card__block.selected.hover .card__product--default {
  display: none;
}
.card__block.selected.hover .card__product--hover {
  display: block;
  color: #e62e7a;
}
.card__block.selected.hover .card__weight {
  background: #e62e7a;
}
.card__block[data-available="disabled"] a {
  cursor: auto;
}
.card__block[data-available="disabled"] .card {
  background: -webkit-linear-gradient(315deg, transparent 30px, #818181 0);
  background: linear-gradient(135deg, transparent 30px, #818181 0);
}
.card__block[data-available="disabled"] .card__wrap:after {
  position: absolute;
  width: 100%;
  height: 100%;
  content: '';
  background: -webkit-linear-gradient(315deg, transparent 28px, #fff 0);
  background: linear-gradient(135deg, transparent 28px, #fff 0);
  top: 0;
  left: 0;
  right: 0;
  opacity: 0.5;
  border-radius: 6px;
}
.card__block[data-available="disabled"] .card__bot-text--default {
  display: none;
}
.card__block[data-available="disabled"] .card__bot-text--disabled {
  display: block;
  color: #ffff66;
}
.card__block[data-available="disabled"] .card .card__weight {
  background: #b3b3b3;
}
.card__block[data-available="disabled"] .card__descr,
.card__block[data-available="disabled"] .card__product,
.card__block[data-available="disabled"] .card__ttl-sub,
.card__block[data-available="disabled"] .card__ttl2 {
  color: #b3b3b3;
}
@media screen and (max-width: 479px) {
  .card__block {
    margin: 0 0 20px;
  }
  .card__wrap {
    padding: 15px 30px;
  }
}

